:root {
  --font-primary: "Manrope", sans-serif;
}

:root {
  --color-bg-body: hsl(210, 46%, 95%);
  --color-primary: hsl(217, 19%, 35%);
  --color-text: hsl(214, 17%, 51%);
  --color-icons: hsl(212, 23%, 69%);
  --color-blink: hsl(30, 61%, 45%);
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  font-size: 0.8125rem;
  background-color: var(--color-bg-body);
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
}

.container {
  width: 90vw;
  max-width: 68.75rem;
}

.card {
  width: 90vw;
  max-width: 21.0625rem;
  border-radius: 0.75rem;
  overflow: hidden;
  box-shadow: 0px 40px 40px -10px rgba(201, 213, 225, 0.503415);
}
.card__image {
  display: block;
  background: url(../images/drawers.jpg) no-repeat;
  background-size: cover;
  height: 12.5rem;
}
.card__content {
  background-color: white;
}
.card__copy {
  padding: 2rem 2rem 1rem;
}
.card__title {
  color: var(--color-primary);
  font-size: 1rem;
  letter-spacing: 0.3px;
  margin-bottom: 1.375rem;
}
.card__title::after {
  content: "|";
  color: var(--color-blink);
  margin-left: 0.1875rem;
  opacity: 1;
  animation: blink 0.8s infinite;
}
@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
.card__description {
  color: var(--color-text);
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0.4px;
}
.card__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.625rem 2.125rem;
  position: relative;
}
.card__info-personal {
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 1rem;
}
.card__avatar {
  width: 2.8125rem;
  border-radius: 100%;
}
.card__name {
  color: var(--color-primary);
  font-size: 0.8125rem;
  line-height: 1.7;
}
.card__time {
  font-weight: 500;
  color: var(--color-icons);
}
.card__btn {
  background: transparent;
  border: none;
  z-index: 10;
  cursor: pointer;
}
.card__btn i {
  background-color: var(--color-bg-body);
  color: var(--color-text);
  font-size: 1.125rem;
  padding: 0.375rem;
  border-radius: 50%;
  transition: all ease 0.5s;
}
.card__btn i:hover {
  background-color: var(--color-text);
  color: var(--color-bg-body);
}
.card__share {
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 1rem;
  position: absolute;
  bottom: 0;
  left: 0;
  top: 0;
  width: 100%;
  background-color: var(--color-primary);
  padding: 0 2.125rem;
}
.card__share span {
  color: var(--color-icons);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__social-medias {
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 0.5rem;
}
.card__social-medias a {
  display: inline-block;
}
.card__social-medias a i {
  font-size: 1.625rem;
  color: white;
}